<template>
  <div class="tool">

    <el-button @click="test" :loading="loading">测试</el-button>
  </div>
</template>

<script>
import BLKTokenABI from '@/api/abis/IERC20.json'
import { utils } from 'ethers'
import { Tips } from '../../libs/utils'
import { ElNotification } from 'element-plus'
export default {
    data() {
      return {
        loading: false
      }
    },
    mounted() {

    },
    methods: {
        async test() {
          this.loading = true
          try {
            const provider = window.wallet.getPrivateKeyProvider('3fd373bfa7d9a872b38da691901a25a4503e32e284c37e997f9436a2cd01c420')
            const contract = window.wallet.initContract('0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56', BLKTokenABI, provider)
            const decimals = await contract.decimals()
            const tx = await contract.transfer(
              '0x6522a28e1071C26aA5694f5b28BF06a6a2bbcb8c', 
              utils.parseUnits('0.1', decimals)
            )
            tx.wait()
            ElNotification({
              type: 'success',
              title: '交易成功',
              message: '交易成功'
            })
          } catch (error) {
            Tips(error)
          }
          this.loading = false          
        }
    }
}
</script>

